import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-modal';
import { t } from 'sn-translate';

export const view = (state) => {
	let props = state.properties;
	if(!props.table || !props.sysId || !state.confs || state.confs.totalMsg <=0)
		return null;

	var title = state.confs.notificationTitle ? state.confs.notificationTitle : "Special Handling Notes";
	title = t('{0}', title);
	return (
		<Fragment>
			<now-modal
				size="md" manage-opened
				opened={state.showModal}
				header-label={title}
				shouldFocusOnOpen={true}>
				<sn-component-special-handling-notes
					confs={state.confs} table={state.properties.table} sys_id={state.properties.sysId} />
			</now-modal>
		</Fragment>
	);
};
