import { createElementFromString } from '@servicenow/ui-renderer-snabbdom';
import {map, isNil} from 'lodash';
import { t } from 'sn-translate';
import '@servicenow/now-button';
import '@servicenow/now-highlighted-value';
import {DISMISS_NOTE} from './constants';

const getPriorityText = (priority) => {
	let text;
	switch (priority) {
	case 1:
		text = t('1 - Critical');
		break;
	case 2:
		text = t('2 - High');
		break;
	case 3:
		text = t('3 - Moderate');
		break;
	case 4:
		text = t('4 - Low');
		break;
	default:
		break;
	}
	return text;
};

const getPriorityStatus = (priority)  => {
	let name;
	switch (priority) {
	case 1:
		name = 'critical';
		break;
	case 2:
		name = 'high';
		break;
	case 3:
		name = 'moderate';
		break;
	case 4:
		name = 'low';
		break;
	default:
		break;
	}
	return name;
};

const messageDetails = (message) => {
	if(isNil(message))
		return '';
    message = "<div>" + message + "</div>"//wrapping the message in a <div> tag as createElementFromString currently returns vnode for only one child element (DEF0068544)
	return createElementFromString(message);
}

const stripHTMLTags = (htmlContent) => {
	if(isNil(htmlContent))
		return '';

	var contentDiv = document.createElement("DIV");
	contentDiv.innerHTML = htmlContent;
	return contentDiv.textContent || contentDiv.innerText || '';
}

const ariaLabel = (content) => {
	return t('Dismiss note : ') + content.short_description + ' ' + stripHTMLTags(content.message);
}


export const view = (state, {dispatch}) => {
	const {table, sysId, confs,} = state.properties;
	if(!table || !sysId || !confs)
		return null;

	let content = confs.content;
	let totalMessageCount =  confs.totalMsg;
	let currentNumber = state.remainingNoteCount && state.remainingNoteCount > 0 ? state.remainingNoteCount : confs.currentNum;
	return (
		<div>
			<div aria-label={t('Special Handling Notes')}>
				{
					map(content, (record, idx) => {
						if(!state.dismissedNotes || !state.dismissedNotes.includes(record.sys_id)) {
							return (
								<div id={record.sys_id} key={idx} className="sn-component-special-handling-notes">
									<div className="sn-component-special-handling-notes-header">
										<div className="sn-component-special-handling-notes-header-content">
											<div className="sn-component-special-handling-notes-header-content-highlighted-value">
												<span>
													<now-highlighted-value show-icon label={getPriorityText(record.priority)} status = {getPriorityStatus(record.priority)} />
												</span>
											</div>
										</div>
										<div className="sn-component-special-handling-notes-header-content-button">
											<now-button size="md" config-aria={{'aria-label': ariaLabel(record)}} on-click = {() => dispatch(DISMISS_NOTE, {tableName: table, record_id : sysId, clicked_id:record.sys_id})} label = {t('Dismiss')}/>
										</div>
									</div>
									<div className="sn-component-special-handling-notes-body">
										<div className="sn-component-special-handling-notes-body-message">
											<span>
												{record.short_description}
											</span>
										</div>
										<div className="sn-component-special-handling-notes-body-description">
											<span>
												{messageDetails(record.message)}
											</span>

										</div>
									</div>
								</div>
							);
						}

					})
				}
			</div>
			<footer className="sn-component-special-handling-notes-footer">
				<div className="sn-component-special-handling-notes-footer-message">
					<span>
						{totalMessageCount == 1 ? t('{0} of {1} message', currentNumber, totalMessageCount) : t('{0} of {1} messages', currentNumber, totalMessageCount)}
					</span>
				</div>
			</footer>
		</div>
	);
};
