import { createHttpEffect } from '@servicenow/ui-effect-http';
import {DISMISS_NOTE, DISMISS_NOTE_SUCCEEDED, DISMISS_ALL} from './constants';

const dismissNoteEffect = createHttpEffect('/api/now/v1/special_handling_notes/alertseen',{
	method: 'PUT',
	queryParams : ['tableName','record_id','clicked_id'],
	successActionType: DISMISS_NOTE_SUCCEEDED
});

const dismissNoteSucceededEffect = ({state, dispatch, action, updateState}) => {
	let dismissedNotes = state.dismissedNotes;
	let remainingNoteCount = state.remainingNoteCount;
	if(!dismissedNotes) {
		dismissedNotes = [];
	}
	if(action.meta && action.meta.request && action.meta.request.params.clicked_id) {
		let clicked_id = action.meta.request.params.clicked_id;
		if(!dismissedNotes.includes(clicked_id)) {
			dismissedNotes.push(clicked_id);
			remainingNoteCount = state.properties.confs.totalMsg - dismissedNotes.length;
		}
	}
	updateState ({
		dismissedNotes: dismissedNotes,
		remainingNoteCount: remainingNoteCount
	})

	if((state.dismissedNotes && state.dismissedNotes.length == state.properties.confs.totalMsg) || (!state.dismissedNotes && state.properties.confs.totalMsg == 1)) {
		dispatch('NOW_MODAL#FOOTER_ACTION_CLICKED');
		dispatch(DISMISS_ALL);
	}
}


export default {
	[DISMISS_NOTE]: dismissNoteEffect,
	[DISMISS_NOTE_SUCCEEDED]: dismissNoteSucceededEffect
};