import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {EDS_MODAL_OPENED_SET, LOAD_NOTES, LOAD_NOTES_SUCCEEDED, LOAD_NOTES_FAILED, DISMISS_ALL} from './constants';
import { shnQuery } from './shnQuery';
import {actionTypes} from '@servicenow/ui-core';


const loadNotesEffect = createGraphQLEffect(shnQuery,{
	successActionType : LOAD_NOTES_SUCCEEDED,
	errorActionType : LOAD_NOTES_FAILED,
	variableList : ['table','sys_id']
});

const loadNotesFailedEffect = () => {
	console.log("Loading notes failed");
};

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED] : ({action, dispatch}) => {
		dispatch(LOAD_NOTES, {table: action.payload.host.table, sys_id: action.payload.host.sysId});
	},
	[EDS_MODAL_OPENED_SET]: ({action, updateState}) => {
		if (action.payload.value == false) {
			updateState ({
				showModal: false
			})
		} else {
			updateState ({
				showModal: true
			})
		}
	},
	[LOAD_NOTES]: loadNotesEffect,
	[LOAD_NOTES_SUCCEEDED]: ({action, updateState}) => {
		if (action.payload && action.payload.data && action.payload.data.GlideSHN_Query) {
			updateState ({
				confs: action.payload.data.GlideSHN_Query,
				showModal: true
			})
		} else {
			updateState ({
				confs: [],
				showModal: false
			})
		}
	},
	[LOAD_NOTES_FAILED]: loadNotesFailedEffect,
	[DISMISS_ALL]: ({updateState}) => {
		updateState ({
			showModal:false,
			type: 'NOW_MODAL#FOOTER_ACTION_CLICKED',
			payload: {}
		})
	}
};